.zs-two-cols-container {
  padding-top: 2rem;
  padding-bottom: 2rem;

  .content-mx-w-560 {
    max-width: 560px;
  }
  .zs-two-cols-main-img-cont {
    display: none !important;
  }
  .zs-two-cols-mobile-img-cont {
    display: block !important;
    max-width: 320px;
    margin-bottom: 1rem;
  }
  @media only screen and (min-width: 650px) {
    .zs-two-cols-main-img-cont {
      display: block !important;
    }
    .zs-two-cols-mobile-img-cont {
      display: none !important;
    }
  }
}
